
.lista-gap {
  display: flex;
  flex-direction: column;
  gap: 32px;

  @include sm {
    gap: 48px;
  }

  .item-noticia {
    display: flex;
    flex-direction: column;
    gap: 16px;
    padding-bottom: 32px;
    border-bottom: solid 1px #dadada;

    @include sm {
      padding-bottom: 48px;
    }

    @include md {
      flex-direction: row;
    }
    
    .item-noticia-img-container {
      width: 100%;
      height: 192px;

      @include sm {
        height: 250px;
      }

      @include md {
        flex-basis: 250px;
        flex-grow: 0;
        flex-shrink: 0;
        width: 250px;
        height: 250px;
      }

      

      .item-noticia-img {
        object-fit: cover;
        object-position: center;
        width: 100%;
        height: 100%;
  

      }
    }

   

    article {
      display: flex;
      flex-direction: column;
      gap: 16px;

      .item-noticia-tag {
        font-weight: 700;
        color: $orange;
      }

      .title-text-container {
        .lista-noticia-title {
          font-family: $montserrat;
          font-weight: 700;
          font-size: 24px;
          line-height: 33px;
        }
      }

      // APAGAR
      .flex-between {
        display: flex;
        justify-content: space-between;
        align-items: center;

        .lista-noticia-data {
          font-size: 14px;
          color: #727272;
        }

        text-align: center;
        .lista-noticia-compartilhar {
          display: flex;
          align-items: center;
          padding: 8px;
          gap: 8px;
          position: relative;

          .arrow-right {
            img {
              width: 15px;
              height: 16px;
            }
          }

          &.active {
            .social-pop-up {
              display: flex;
            }
          }

          .social-pop-up {
            display: none;
            background: #ffffff;
            border: #dadada solid 1px;
            padding: 8px;
            border-radius: 4px;
            height: auto;
            width: auto;
            position: absolute;
            left: 0%;
            top: 36px;
            transform: translateX(-50%);
            gap: 16px;

            a {
              display: flex;
              justify-content: center;
              align-items: center;
              width: 45px;
              height: 45px;

              img {
                display: flex;
                height: 100%;
                width: 100%;
              }
            }
          }

          .lista-link-compartilhar {
            font-weight: 700;

            @include lg {
              &:hover {
                text-decoration: underline;
              }
            }
          }
        }
      }
    }
  }
}
