.flex-between {
  display: flex;
  justify-content: space-between;
  align-items: center;

  .lista-noticia-data {
    font-size: 14px;
    color: #727272;
  }

  text-align: center;
  .lista-noticia-compartilhar {
    display: flex;
    align-items: center;
    padding: 8px;
    gap: 8px;
    position: relative;

    .arrow-right {
      img {
        width: 15px;
        height: 16px;
      }
    }

    &.active {
      .social-pop-up {
        display: flex;
      }
    }

    .social-pop-up {
      display: none;
      background: #ffffff;
      border: #dadada solid 1px;
      padding: 8px;
      border-radius: 4px;
      height: auto;
      width: auto;
      position: absolute;
      left: 0%;
      top: 36px;
      transform: translateX(-50%);
      gap: 16px;

      a {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 45px;
        height: 45px;

        img {
          display: flex;
          height: 100%;
          width: 100%;
        }
      }
    }

    .lista-link-compartilhar {
      font-weight: 700;

      @include lg {
        &:hover {
          text-decoration: underline;
        }
      }
    }
  }
}