.lista-informes-corporativos {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 48px;

  .informes-container {
    padding-bottom: 32px;
    border-bottom: solid #dadada 1px;
    a {
      display: flex;
      flex-direction: column;
      gap: 16px;

      .informes-tag {
        font-weight: 700;
        color: $orange;
      }

      .informes-title {
        font-family: $montserrat;
        font-weight: 700;
        font-size: 24px;
        line-height: 33px;
      }

      .informes-p {
        margin-bottom: 16px;
      }
    }
  }
}
