.paginacao-container {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0px 0px 64px 0px;

  @include md {
    margin: 16px 0px;
  }

  @include lg {
    margin: 16px 0px 32px 0px;
  }

  nav {
    .pagination {
      display: flex;
      align-items: center;

      .page-item {
        display: flex;
        justify-content: center;
        align-items: center;

        height: 60px;
        width: auto;

        &.active {
          height: 32px;
          padding: 0;
          width: 32px;
          border-radius: 100px;
          color: #ffffff;
          background: $orange;
        }

        .page-link {
          padding: 0px 12px;
          display: flex;
          justify-content: center;
          align-items: center;
          height: 100%;
          width: 100%;

          @include sm {
            padding: 0px 24px;
          }

          @include lg {
            &:hover {
              text-decoration: underline;
            }
          }
        }

        
      }
    }

    & > * {
      font-weight: 700;
    }
  }
}
