.banner-container-top {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 2rem 0rem;

  & > * {
    flex: none;
  }
}

.banner-container-side {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 2rem 0rem;

  & > * {
    flex: none;
  }

  @include lg {
    margin: 0rem auto 2rem 0rem ;
  }
}

