.generic-container {
  margin-bottom: 4rem;
}

.text-orange {
  color: $orange;
}

.categoria-container {
  display: flex;
  // SE DER TUDO RUIM
  justify-content: flex-start;
  // SE DER TUDO RUIM
  align-items: center;
  margin: 0.5rem 0rem;
  padding: 0rem 1rem;
  gap: 8px;

  @include sm {
    font-size: $f-base;
    padding: 0rem 2rem;
  }

  @include md {
    padding: 0rem 3rem;
  }

  @include xl2 {
    margin: auto;
    width: 75rem;
    padding: 0;
  }

  .categoria-text {
    font-weight: 700;
  }
}

.line-after::after {
  content: "";
  position: absolute;
  width: 5rem;
  height: 0.25rem;
  background: $orange;
  left: 0rem;
  bottom: -0.25rem;
  border-radius: 624.9375rem;
}

.justify-container {
  margin: 1rem 0rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.data-text {
  margin-left: 0.5rem;
  color: $darkGrey;
  font-size: $f-min;

  @include sm {
    font-size: $f-base;
  }
}

.clock {
  width: 1rem;
  height: 1.125rem;
}

.mobile-title {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: $f-semi;
  position: relative;
}

.mb-48px {
  margin-bottom: 3rem;
}

.cms-text {
  font-size: $f-base;
  font-weight: 700;
  color: $orange;
}

.flex {
  align-items: center;
  display: flex;
}

.noticia-img {
  height: 13.5rem;
  width: 100%;
  object-fit: cover;

  @include sm {
    height: 18.75rem;
  }

  @include md {
    height: 25rem;
  }

  @include lg {
    height: 18.75rem;
  }
}

.text-anuncio {
  color: #0ea5e9;
  font-weight: 700;
}

.leia-mais-container {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 1.5rem;

  .leia-mais-btn {
    width: 6rem;
    text-align: center;
    padding: 0.25rem 0.75rem;
    font-weight: 700;
    background: $orange;
    color: $white;
    border-radius: 0.25rem;
    /// JS
    display: none;

    @include xl {
      &:hover {
        background: $lightOrange;
      }

      &:active {
        background: $orange;
      }
    }
  }
}

.destaque-border {
  padding: 32px;
  border: solid 1px $orange;
  border-radius: 0.25rem;
}

