.home-title {
  font-weight: 700;
  font-size: 24px;
  font-family: $montserrat;
}

#hero {
  padding: 0 16px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 32px;

  @include sm {
    padding: 16px 32px 0 32px;
  }

  @include xl {
    padding: 0;
    margin: auto;
    width: 1110px;
  }

  .hero-container {
    display: flex;
    flex-direction: column;
    gap: 16px;

    @include lg {
      flex-direction: row;
      height: auto;
      margin-bottom: 24px;
    }

    @include xl {
      width: 100%;
    }

    .noticia-principal {
      display: flex;
      flex-direction: column;
      gap: 16px;
      margin: 16px 0;

      @include md {
        height: 350px;
        position: relative;
      }

      @include lg {
        margin: 0;
      }

      @include xl {
        flex: 1;
        height: 509px;
      }

      .image-container {
        position: relative;

        img {
          width: 100%;
          height: 250px;
          object-fit: cover;
          object-position: center;

          @include md {
            height: 350px;
          }

          @include xl {
            height: 509px;
          }
        }

        .overlay {
          position: absolute;
          right: 0;
          top: 0;

          @include md {
            width: 100%;
            height: 100%;
            background: linear-gradient(
              180deg,
              rgba(0, 0, 0, 0.1) 0%,
              rgba(0, 0, 0, 0.7) 100%
            );
          }

          @include lg {
            background: none;
          }

          @include xl {
            background: linear-gradient(
              180deg,
              rgba(0, 0, 0, 0.1) 0%,
              rgba(0, 0, 0, 0.7) 100%
            );
          }
        }
      }

      .infos {
        display: flex;
        flex-direction: column;
        gap: 8px;

        @include md {
          position: absolute;
          bottom: 0;
          left: 0;
          padding: 24px;
          width: 100%;

          @include lg {
            position: inherit;
            padding: 0;
          }

          @include xl {
            position: absolute;
            padding: 24px;
          }
        }

        .tag {
          font-weight: 700;
          color: $orange;
          font-size: 18px;
        }

        .titulo {
          font-size: 24px;
          font-weight: 700;
          font-family: $montserrat;
          line-height: 29px;

          @include md {
            color: $white;
          }

          @include lg {
            color: $black;
          }

          @include xl {
            color: $white;
          }
        }

        .desc {
          @include md {
            color: $white;
          }

          @include lg {
            color: $black;
          }

          @include xl {
            color: $white;
          }
        }
      }
    }

    .sub-noticias {
      display: flex;
      flex-direction: column;
      gap: 32px;

      @include sm {
        display: grid;
        grid-template-columns: repeat(2, minmax(0, 1fr));
      }

      @include lg {
        gap: 16px;
      }

      .noticia-item {
        padding-bottom: 32px;
        border-bottom: 1px solid $grey;

        @include lg {
          padding-bottom: 0;
          border: none;
          width: 160px;
        }

        .noticia-container {
          display: flex;
          flex-direction: column;
          gap: 8px;

          .img-container {
            position: relative;

            @include lg {
              img {
                height: 94px;
              }
            }

            .tag {
              font-weight: 700;
              color: $white;
              position: absolute;
              top: 0;
              left: 0;
              padding: 8px;
              background: $orange;
              border-radius: 0 0 4px 0;

              @include lg {
                font-size: 12px;
                padding: 4px 8px;
              }
            }
          }

          .titulo {
            font-size: 24px;
            font-weight: 700;

            @include lg {
              font-size: 16px;
              line-height: 19px;
            }
          }
        }
      }
    }
  }

  .mais-noticias {
    font-weight: 700;
    color: $white;
    padding: 8px 16px;
    border-radius: 4px;

    background: $orange;
  }
}

#ferramentas {
  display: flex;
  flex-direction: column;
  gap: 32px;

  padding: 0 32px;

  @include sm {
    gap: 40px;
  }

  @include xl {
    padding: 0;
    margin: auto;
    width: 1110px;
  }

  h2 {
    text-align: center;
  }

  .lista-ferramentas {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 23px;
    align-items: flex-start;

    .item-ferramenta {
      display: flex;
      flex-direction: column;
      gap: 8px;
      width: 103px;
      height: 103px;
      align-items: center;

      img {
        height: 29px;
      }

      .ferramenta-nome {
        font-weight: 700;
        text-align: center;
      }
    }
  }
}

#podcast {
  display: flex;
  flex-direction: column;
  gap: 32px;

  padding: 0 16px;

  @include sm {
    padding: 0 32px;
    gap: 40px;
  }

  @include xl {
    padding: 0;
    margin: auto;
    width: 1110px;
  }

  h2 {
    text-align: center;

    @include md {
      text-align: left;
    }
  }

  .ver-mais {
    font-weight: 700;
    text-decoration: underline;
  }
}

#colunistas {
  display: flex;
  flex-direction: column;
  gap: 32px;

  padding: 0 16px;

  @include sm {
    padding: 0 32px;
  }

  @include xl {
    padding: 0;
    margin: auto;
    width: 1110px;
  }

  h2 {
    text-align: center;

    @include md {
      text-align: left;
    }
  }

  .colunistas-container {
    display: flex;
    flex-direction: column;
    gap: 40px;

    @include lg {
      display: grid;
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }

    .colunista-item {
      display: flex;
      flex-direction: column;
      align-items: center;

      @include sm {
        flex-direction: row;
        gap: 32px;
        align-items: flex-start;
      }

      .colunista-figure {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 16px;

        @include sm {
          flex: none;
        }

        img {
          width: 161px;
          height: 161px;
          object-fit: cover;
          object-position: center;
        }

        .colunista-nome {
          font-family: $montserrat;
          font-size: 18px;
          font-weight: 700;
          text-align: center;
        }
      }

      .colunista-article {
        display: flex;
        flex-direction: column;
        gap: 8px;
        align-items: center;

        .colunista-categoria {
          font-weight: 700;
          color: $orange;
          font-size: 18px;

          @include sm {
            text-align: left;
            width: 100%;
          }
        }

        .colunista-titulo {
          font-family: $montserrat;
          font-weight: 700;
          font-size: 18px;
          text-align: center;

          @include sm {
            text-align: left;
          }
        }

        .colunista-desc {
          text-align: center;

          @include sm {
            text-align: left;
          }
        }
      }
    }
  }
}

#parceiros {
  display: flex;
  flex-direction: column;
  gap: 32px;
  padding: 0 16px;

  @include sm {
    padding: 0 32px;
    gap: 40px;
  }

  @include xl {
    padding: 0;
    margin: auto;
    width: 1110px;
  }

  h2 {
    text-align: center;

    @include lg {
      text-align: left;
    }
  }

  .parceiros-container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 30px;

    @include sm {
      display: grid;
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }

    @include md {
      display: flex;
    }

    @include lg {
      justify-content: space-between;
    }

    .parceiro-item {
      img {
        width: 255px;
        height: 192px;

        @include lg {
          width: 215px;
          height: 161.88px;
        }

        @include xl {
          width: 255px;
          height: 192px;
        }
      }
    }
  }
}

#videos {
  background-color: #333333;
  .dark-container {
    background: #333333;
    padding: 60px 16px;
    display: flex;
    flex-direction: column;
    gap: 32px;

    @include sm {
      padding: 60px 32px;
    }

    @include xl {
      padding: 60px 0;
      margin: auto;
      width: 1110px;
      gap: 30px;
    }

    .videos-max-container {
      display: flex;
      flex-direction: column;
      gap: 32px;

      @include lg {
        flex-direction: row;
        gap: 32px;
      }

   

      .video-principal {
        display: flex;
        flex-direction: column;
        gap: 8px;

        @include sm {
          gap: 16px;
        }

        @include lg {
          flex: 0 0 750px;
        }


        @include xl {
          flex: 0 0 825px;
        }

        .figure-principal {
          position: relative;

          .tumb-principal {
            width: 100%;
            height: auto;
          }

          .player-ilu {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            width: 60px;
            height: 60px;
          }
        }

        .principal-video-titulo {
          font-weight: 700;
          color: $white;
          font-size: 18px;

          @include sm {
            font-size: 24px;
          }
        }

        .principal-desc-video {
          color: $white;
          line-height: 31.5px;
        }
      }

      .sub-videos {
        display: flex;
        flex-direction: column;
        gap: 16px;

        @include sm {
          flex-direction: row;
          gap: 24px;
        }

        @include lg {
          flex-direction: column;
        }

        .sub-video-item {
          display: flex;
          flex-direction: column;
          gap: 8px;
        }

        .sub-video-titulo {
          font-weight: 700;
          color: $white;
        }
      }
    }

    .inscrever-link {
      font-weight: 700;
      color: $white;
      padding-top: 16px;
      font-size: 24px;
      position: relative;
      text-align: center;
      width: 100%;

      &::before {
        content: "";
        transform: translateX(-50%);
        position: absolute;
        left: 50%;
        top: 0px;
        background: $white;
        width: 100%;
        height: 1px;
      }
    }
  }
}
