.generic-container.destaque-border.center-calculadora {
  margin: auto;
  max-width: 768px;
}

.bold-black {
  color: #000;
  font-weight: 700;
}

.calculadora-resultados {
  width: 100%;
  display: flex;
  flex-direction: column;
  margin: 24px 0;
  gap: 24px;

  .item-resultado {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
    justify-content: center;

    @include sm {
      gap: 8px 64px;
    }

    @include md {
      gap: 16px 96px;
    }

    @include lg {
      gap: 16px 64px;
    }

    @include xl {
      gap: 16px 128px;
    }
    
    .resultado-titulo {
      font-family: $montserrat;
      width: 100%;
      font-weight: 700;
      text-align: center;
      font-size: 18px;
    }

    .resultado {
      font-weight: 700;
      text-align: center;
      font-size: 16px;
    }
  }
  
}

.extra-info {
  text-align: center;
  font-size: 14px;
  margin: 24px 32px 8px 32px;
  color: #888;
}