.lista-artigo {
  display: flex;
  flex-direction: column;
  gap: 48px;



.blog-artigo {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  gap: 16px;
  padding-bottom: 48px;
  border-bottom: #dadada solid 1px;

  @include md {
    flex-direction: row;
    gap: 32px;
    align-items: flex-start;
  }

  .blog-small-column {
    .blog-img {
      width: 250px;
      height: 250px;

      @include md {
        width: 200px;
        height: 200px;
      }
    }

    @include md {
      min-width: 200px;
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    .tiutlo {
      font-weight: 700;
      text-align: center;
      font-size: 24px;
      font-family: $montserrat;
      color: $orange;
      text-align: center;
      margin-top: 16px;
      
    }

    .description-small {
      font-weight: 700;
      text-align: center;
    }
  }

  .blog-big-column {
    display: flex;
    flex-direction: column;
    gap: 16px;

    .titulo {
      font-family: $montserrat;
      font-weight: 700;
      font-size: 24px;
    }
  }
}

}