.search-box {
  display: flex;
  justify-content: space-between;
  width: 100%;
  border-radius: 100px;
  height: 48px;
  margin: 32px 0px;
  padding: 0px 16px;

  @include sm {
    padding: 0px 32px;
  }

  @include sm {
    font-size: $f-base;
  }

  @include md {
    padding: 0rem 3rem;
  }

  @include lg {
    margin: 48px 0;
  }

  @include xl2 {
    margin: 48px auto;
    width: 75rem;
    padding: 0;
  }

  input {
    text-indent: 1rem;
    border-radius: 100px 0px 0px 100px;
    width: 100%;
    background: #f8fafc;

    @include sm {
      text-indent: 24px;
    }

    &:focus {
      border: none;
      outline: none;
    }
  }

  .search-btn {
    border-radius: 0px 100px 100px 0px;
    width: 6rem;
    height: 48px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #f8fafc;

    img {
      width: 25px;
      height: 25px;
    }
  }
}
